export default {
  props: [],
  head() {
    return {
      title: 'Homepage ',
      meta: [
        {
          hid: 'Homepage ',
          name: 'Homepage ',
          content:
            'This website features are latest code fixes, tutorials and reviews, news about technology and programming also includes programming quotes and world event anniversaries. May also include personal blogs and top 10 - 100 reviews of the authors favorite things and current researches.'
        }
      ]
    }
  },
  data: () => ({
    transparent: 'rgba(255, 255, 255, 0)',
    blogs_load: false,
    news_load: false,
    posts_load: false,
    selection: 0,
    content: [],
    news: [],
    posts: [],
    nuxt: [],
    selectedItem: 0,
    show: true,
    title: '',
    model: 6,
    rounded: ['0', 'sm', 'md', 'lg', 'xl', 'pill', 'circle'],
    links: [],

    posts_content: 0,
    news_content: 0,

    tags: [],
    load_tags: false,
    total_tags: 0,
    height: '200px',
    blogs_load: false,
    blogs_content: [],
    blogs_total: 0,

    nuxt: [],
    laravel: [],
    vue: [],
    load_nuxt: false,
    load_laravel: false,
    load_vue: false,

    show_nuxt: false,
    show_laravel: false,
    show_vue: false,
    blogs_in_random: [],
    blogs_in_latest: [],
    page: 2
  }),
  layout: 'default',
  methods: {
    async nextArticle() {
      this.blogs_load = true
      try {
        this.$axios
          .$get(`api/blog/page/${this.page}/item/${10}`)
          .then(res => {
            this.blogs_total = res.data.length
            this.blogs_content = [...this.blogs_content, ...res.data]
            this.page = this.page + 1
            this.blogs_load = false
          })
          .catch(error => {
            this.blogs_load = false
          })
          .finally(() => {})
      } catch (error) {
        this.blogs_load = false
        console.log('error')
      }
    }
  },
  computed: {},
  async fetch() {
    this.blogs_load = true
    const blogs = await this.$axios.$get(`api/blog/page/1/item/10`)
    this.blogs_load = false
    this.blogs_content = blogs.data
    this.blogs_total = blogs.data.length

    // this.load_nuxt = true
    // const nuxt = await this.$axios.$get(`api/blog/page/1/item/5/tags/nuxt`)
    // this.load_nuxt = false
    // this.nuxt = nuxt.data
    // this.show_nuxt = true

    // this.load_laravel = true

    // const laravel = await this.$axios.$get(
    //   `api/blog/page/1/item/5/tags/Laravel`
    // )
    // this.load_laravel = false
    // this.laravel = laravel.data
    // this.show_laravel = true
    // this.load_vue = true

    // const vue = await this.$axios.$get(`api/blog/page/1/item/5/tags/vue3`)
    // this.load_vue = false
    // this.vue = vue.data
    // this.show_vue = true

    const BlogsRandom = await this.$axios.$get(`api/blog/index/10`)
    this.blogs_in_random = BlogsRandom.data

    const Blogslatest = await this.$axios.$get(`api/blog/latest/10`)
    this.blogs_in_latest = Blogslatest.data

    this.load_tags = true
    const tags = await this.$axios.$get(`api/tags`)
    this.load_tags = false
    this.tags = tags.data
    this.total_tags = tags.total
  }
}
